
div#app {
  width: 100%;
  font-family: 'Titillium Web', sans-serif;
  /*font-family: 'Lato', sans-serif;*/
}
div#spinner {
  width: 10px;
  margin: auto;
  margin-top: 50px;
}
nav {
  text-align: center;
  margin-top: 30px;
  font-size: 125%;
  border-bottom: 1px solid #bbb;
  padding-bottom: 5px;
}
nav a {
  color: #000;
